.navbar {
    width: 100%;
    height: 100px;
    position:sticky;
    top: 0;
    background: #21325e;
    z-index: 500;
  
}


.links {
    width: 100%;
    height: 100%;
    display:flex;
    align-items: center;
    justify-content: center;
    color: white;
   
}

.navbar a {
    color: white;
    text-decoration: none;
    margin: 20px;
    font-size: 25px;
    background-color: #21325e;
  }
  .navbar a:hover
  {
    color: rgb(193, 191, 191);
  }
  .navbar a:active
  {
    color: black;
  }

  
  .toggleButton {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  .toggleButton svg {
    font-size: 50px;
  }

  .toggleButton button {
    margin-right: 20px;
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
  }

  #open {
    height: 100vh;
  }
  
  #open .links {
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;
    
  }
  
  #open a {
    width: 100%;
    text-align: center;
  }

  

  @media only screen and (max-width: 900px) {
    .navbar a {
      width: 70px;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .toggleButton {
      display: flex;
    }
    #close a {
      display: none;
    }
  }
  
  @media only screen and (min-width: 600px) {
    .toggleButton {
      display: none; 
    }
  
    .hiddenLinks {
      display: none;
    }
  
    #open {
      height: 100px;
    }
  
    #open .links {
      flex-direction: row;
    }
  }